import * as type from './actions/actionTypes';

const initialState = {
  todoList: [],
  userInfo: {},
  todoItem: {},
  dialogOpen: false,
  dialogType: '',
  currentRoute: ''
}

const reducer = (state = initialState, action) => {
  switch(action.type) {
    case type.GET_TODO_LIST:
    case type.ADD_TODO_ITEM:
    case type.UPDATE_TODO_ITEM:
    case type.DELETE_TODO_ITEM:
      return {
        ...state,
        todoList: [...action.todoList]
      }
    case type.GET_USER_INFO:
      return {
        ...state,
        userInfo: {...action.userInfo}
      }
    case type.ADD_LOCATION_TODO_ITEM:
      return {
        ...state,
        todoList: [...state.todoList, action.payload]
      }
    case type.UPDATE_LOCATION_TODO_ITEM:
      return {
        ...state,
        todoList: [...state.todoList].map(item => item.key === action.payload.key ? action.payload : item)
      }
    case type.DELETE_LOCATION_TODO_ITEM:
      return {
        ...state,
        todoList: [...state.todoList].filter(item => item.key !== action.payload.key)
      }
    case type.SET_TODO_ITEM:
      return {
        ...state,
        todoItem: {
          ...state.todoItem,
          ...action.payload
        }
      }
    case type.OPEN_DIALOG:
      return {
        ...state,
        dialogOpen: true
      }
    case type.CLOSE_DIALOG:
      return {
        ...state,
        dialogOpen: false,
        dialogType: '',
        todoItem: {}
      }
    case type.SET_DIALOG_TYPE:
      return {
        ...state,
        dialogType: action.payload.dialogType
      }
    case type.SET_CURRENT_ROUTE:
      return {
        ...state,
        currentRoute: action.payload.currentRoute
      }
    default:
      return state
  }
}

export default reducer



