import * as actionTypes from './actionTypes'

export const addLocationTodoItem = (item) => {
  const newItem = {
    ...item,
    isLoading: true
  }

  return {
    type: actionTypes.ADD_LOCATION_TODO_ITEM,
    payload: newItem
  }
}

export const updateLocationTodoItem = (item) => {
  const newItem = {
    ...item,
    isLoading: true
  }

  return {
    type: actionTypes.UPDATE_LOCATION_TODO_ITEM,
    payload: newItem
  }
}

export const deleteLocationTodoItem = (item) => {
  return {
    type: actionTypes.DELETE_LOCATION_TODO_ITEM,
    payload: item
  }
}

export const setTodoItem = (item) => {
  return {
    type: actionTypes.SET_TODO_ITEM,
    payload: item
  }
}

export const openDialog = () => {
  return {
    type: actionTypes.OPEN_DIALOG
  }
}

export const closeDialog = () => {
  return {
    type: actionTypes.CLOSE_DIALOG
  }
}

export const setDialogType = (dialogType) => {
  return {
    type: actionTypes.SET_DIALOG_TYPE,
    payload: {dialogType}
  }
}

export const setCurrentRoute = (currentRoute) => {
  return {
    type: actionTypes.SET_CURRENT_ROUTE,
    payload: {currentRoute}
  }
}



