import React from 'react'

// datetime picker
import MuiPickersUtilsProvider from 'material-ui-pickers/utils/MuiPickersUtilsProvider'
import DateTimePicker from 'material-ui-pickers/DateTimePicker'
// pick utils
import DateFnsUtils from 'material-ui-pickers/utils/date-fns-utils'

import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft'
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight'
import DateRange from '@material-ui/icons/DateRange'
import { withStyles } from '@material-ui/core/styles'

import AppInputAdornment from './AppInputAdornment'

const styles = theme => ({
  fullWidth: {
    width: '100%'
  }
})

const AppDateTimePicker = (props) => {
  const {classes, selectedDate, handleDateChange, disabled} = props

  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <DateTimePicker
        autoOk
        className={classes.fullWidth}
        ampm={false}
        showTabs={false}
        autoSubmit={false}
        value={selectedDate}
        onChange={handleDateChange}
        label='Dateline'
        format='YYYY/MM/DD HH:mm'
        leftArrowIcon={<KeyboardArrowLeft />}
        rightArrowIcon={<KeyboardArrowRight />}
        InputProps={{
          startAdornment: <AppInputAdornment position='start' icon={<DateRange />} />
        }}
        disabled={disabled} />
    </MuiPickersUtilsProvider>
  )
}

export default withStyles(styles)(AppDateTimePicker)



