import * as actionTypes from './actionTypes'

const API_SERVICE = 'https://us-central1-the-f2e-service.cloudfunctions.net'

export const getGithubInfo = () => {
  return (dispatch) => {
    fetch('https://api.github.com/users/sky172839465')
    .then(res => res.json())
    .then(data => {
      dispatch({
        type: actionTypes.GET_USER_INFO,
        userInfo: data
      })
    })
  }
}

export const getTodoList = (dataType = 'MY_TASKS') => {
  return (dispatch) => {
    const API = `${API_SERVICE}/getTodoList?dataType=${dataType}`

    fetch(API)
    .then(res => res.json())
    .then(data => {
      dispatch({
        type: actionTypes.GET_TODO_LIST,
        todoList: data.result
      })
    })
  }
}

export const addTodoItem = (payload, dataType = 'MY_TASKS') => {
  return (dispatch) => {
    const API = `${API_SERVICE}/addTodoItem?dataType=${dataType}`

    fetch(API, {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'        
      },
      method: 'POST',
      body: JSON.stringify(payload)
    })
    .then(res => res.json())
    .then(data => {
      dispatch({
        type: actionTypes.ADD_TODO_ITEM,
        todoList: data.result
      })
    })
  }
}

export const updateTodoItem = (payload, dataType = 'MY_TASKS') => {
  return (dispatch) => {
    const API = `${API_SERVICE}/updateTodoItem?dataType=${dataType}`
    payload.newTodoItem.createDT = new Date()

    fetch(API, {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'        
      },
      method: 'POST',
      body: JSON.stringify(payload)
    })
    .then(res => res.json())
    .then(data => {
      dispatch({
        type: actionTypes.UPDATE_TODO_ITEM,
        todoList: data.result
      })
    })
  }
}

export const deleteTodoItem = (payload, dataType = 'MY_TASKS') => {
  return (dispatch) => {
    const API = `${API_SERVICE}/deleteTodoItem?dataType=${dataType}`

    fetch(API, {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'        
      },
      method: 'POST',
      body: JSON.stringify(payload)
    })
    .then(res => res.json())
    .then(data => {
      dispatch({
        type: actionTypes.DELETE_TODO_ITEM,
        todoList: data.result
      })
    })
  }
}



