import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import * as CONSTANT from '../constant'

import AppList from './common/AppList'
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';

const styles = () => ({
  message: {
    height: '80vh'
  }
})

class InProgressContent extends Component {
  constructor () {
    super()
    this.state = {}
  }

  componentDidMount () {
    const {getTodoList} = this.props
    getTodoList(CONSTANT.IN_PROGRESS)
  }

  todoListIsEmpty = () => {
    const {classes} = this.props

    return (
      <Grid
        container
        spacing={0}
        direction='row'
        alignItems='center'
        className={classes.message}>
        <Grid item xs={12}>
          <Typography align='center' variant='display1'>No Task in progress !</Typography>
        </Grid>
      </Grid>
    )
  }

  todoListIsExist = () => {
    const {
      todoList,
      onUpdate,
      onUpdateLocation,
      openDialog,
      setDialogType,
      setTodoItem,
      currentRoute
    } = this.props

    return (
      <AppList
        todoList={todoList} 
        onUpdate={onUpdate}
        onUpdateLocation={onUpdateLocation}
        openDialog={openDialog}
        setDialogType={setDialogType}
        setTodoItem={setTodoItem}
        currentRoute={currentRoute} />
    )
  }

  render () {
    const {todoList} = this.props

    return (
      <div>
        {todoList.length === 0 ? this.todoListIsEmpty() : this.todoListIsExist()}
      </div>
    )
  }
}

export default withStyles(styles)(InProgressContent)



