import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import MyTasksContent from "../component/MyTasksContent";

class MyTasks extends Component {
  render () {
    const {
      todoList,
      getTodoList,
      updateTodoItem,
      updateLocationTodoItem,
      openDialog,
      setDialogType,
      setTodoItem,
      currentRoute
    } = this.props

    return (
      <main>
        <MyTasksContent
          todoList={todoList}
          getTodoList={getTodoList}
          onUpdate={updateTodoItem}
          onUpdateLocation={updateLocationTodoItem}
          openDialog={openDialog}
          setDialogType={setDialogType}
          setTodoItem={setTodoItem}
          currentRoute={currentRoute} />
      </main>
    );
  }
}

const mapStateToProps = state => {
  return {
    todoList: state.todoList,
    currentRoute: state.currentRoute
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getTodoList: (dataType) => dispatch(actionCreators.getTodoList(dataType)),
    updateTodoItem: (payload, dataType) => dispatch(actionCreators.updateTodoItem(payload, dataType)),
    updateLocationTodoItem: (item) => dispatch(actionCreators.updateLocationTodoItem(item)),
    openDialog: () => dispatch(actionCreators.openDialog()),
    setDialogType: (dialogType) => dispatch(actionCreators.setDialogType(dialogType)),
    setTodoItem: (item) => dispatch(actionCreators.setTodoItem(item)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(MyTasks);


