import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import EditDialogContent from "../component/EditDialogContent";

class EditDialog extends Component {
  render () {
    const {
      addTodoItem,
      updateTodoItem,
      deleteTodoItem,
      addLocationTodoItem,
      updateLocationTodoItem,
      deleteLocationTodoItem,
      setTodoItem,
      todoItem,
      openDialog,
      closeDialog,
      dialogOpen,
      dialogType,
      setDialogType,
      currentRoute
    } = this.props

    return (
      <main>
        <EditDialogContent
          addTodoItem={addTodoItem}
          updateTodoItem={updateTodoItem}
          deleteTodoItem={deleteTodoItem}
          addLocationTodoItem={addLocationTodoItem}
          updateLocationTodoItem={updateLocationTodoItem}
          deleteLocationTodoItem={deleteLocationTodoItem}
          setTodoItem={setTodoItem}
          todoItem={todoItem}
          openDialog={openDialog}
          closeDialog={closeDialog}
          dialogOpen={dialogOpen}
          dialogType={dialogType}
          setDialogType={setDialogType}
          currentRoute={currentRoute} />
      </main>
    );
  }
}

const mapStateToProps = state => {
  return {
    todoItem: state.todoItem,
    dialogOpen: state.dialogOpen,
    dialogType: state.dialogType,
    currentRoute: state.currentRoute
  }
}

const mapDispatchToProps = dispatch => {
  return {
    addTodoItem: (payload, dataType) => dispatch(actionCreators.addTodoItem(payload, dataType)),
    updateTodoItem: (payload, dataType) => dispatch(actionCreators.updateTodoItem(payload, dataType)),
    deleteTodoItem: (payload, dataType) => dispatch(actionCreators.deleteTodoItem(payload, dataType)),
    addLocationTodoItem: (item) => dispatch(actionCreators.addLocationTodoItem(item)),
    updateLocationTodoItem: (item) => dispatch(actionCreators.updateLocationTodoItem(item)),
    deleteLocationTodoItem: (item) => dispatch(actionCreators.deleteLocationTodoItem(item)),
    setTodoItem: (item) => dispatch(actionCreators.setTodoItem(item)),
    openDialog: (item) => dispatch(actionCreators.openDialog(item)),
    closeDialog: (item) => dispatch(actionCreators.closeDialog(item)),
    setDialogType: (dialogType) => dispatch(actionCreators.setDialogType(dialogType))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(EditDialog);


