import React, { Component } from 'react'
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom'
import { withStyles } from '@material-ui/core/styles'

import Grid from '@material-ui/core/Grid'

import RouteConfig from './RouteConfig'
// import NoFoundPage from './component/404';
import AppNavs from './container/AppNav'
import EditDialog from './container/EditDialog';


const styles = theme => ({
  root: {
    marginTop: theme.spacing.unit * 15
  }
})

class App extends Component {
  render () {
    const {classes} = this.props

    return (
      <BrowserRouter>
        <div className='App'>
          <AppNavs />
          <main className={classes.root}>
            <Grid container spacing={0} justify='center'>
              <Grid item xs={12} sm={7} md={5} >
                <Switch>
                  <Route exact path='/' render={() => <Redirect to={RouteConfig[0].path} />} />
                  {RouteConfig.map(route => {
                    return <Route path={route.path} component={route.component} key={route.page} />
                  })}
                  <Redirect from="*" to={RouteConfig[0].path} />
                </Switch>
              </Grid>
            </Grid>
          </main>
          <EditDialog />
        </div>
      </BrowserRouter>
    )
  }
}

export default withStyles(styles)(App)



