import MyTasks from './container/MyTasks'
import InProgress from './container/InProgress'
import Canceled from './container/Canceled'
import * as CONSTANT from './constant'

const RouteConfig = [
  { page: 'My Tasks', path: '/MyTasks', component: MyTasks, constant: CONSTANT.MY_TASKS },
  { page: 'In Progress', path: '/InProgress', component: InProgress, constant: CONSTANT.IN_PROGRESS },
  { page: 'Canceled', path: '/Canceled', component: Canceled, constant: CONSTANT.CANCELED },
]

export default RouteConfig



