import React, {Component} from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import * as CONSTANT from '../../constant'
import format from 'date-fns/format'
import formatDistance from 'date-fns/formatDistance'

import List from '@material-ui/core/List'
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItem from '@material-ui/core/ListItem'
import Divider from '@material-ui/core/Divider'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import Checkbox from '@material-ui/core/Checkbox';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import CircularProgress from '@material-ui/core/CircularProgress';

import StarIcon from '@material-ui/icons/Star'
import StarBorderIcon from '@material-ui/icons/StarBorder'
import MoreVertIcon from '@material-ui/icons/MoreVert'
import InfoIcon from '@material-ui/icons/Info'
import EditIcon from '@material-ui/icons/Edit'
import DeleteIcon from '@material-ui/icons/Delete'

import blue from '@material-ui/core/colors/blue'
import green from '@material-ui/core/colors/green'
import red from '@material-ui/core/colors/red'

const styles = theme => ({
  root: {
    width: 'calc(100% - 1rem)',
    margin: '0.5rem',
    backgroundColor: theme.palette.background.paper,
  },
  [CONSTANT.DETAIL]: {
    color: blue[700]
  },
  [CONSTANT.UPDATE]: {
    color: green[700]
  },
  [CONSTANT.DELETE]: {
    color: red[700]
  },
  comment: {
    textOverflow: 'ellipsis',
    width: '80%',
    whiteSpace: 'nowrap',
    overflow: 'hidden'
  },
  taskDone: {
    textDecoration: 'line-through'
  }
})

class AppList extends Component {
  constructor () {
    super()
    this.state = {
      anchorEl: null,
      editItem: {}
    }
  }

  updateTodoItem = (item, column) => (event) => {
    const {onUpdate, onUpdateLocation, currentRoute} = this.props
    const payload = {
      key: item.key,
      newTodoItem: {
        ...item,
        [column]: !item[column]
      }
    }

    onUpdate(payload, currentRoute)
    onUpdateLocation({
      ...payload.newTodoItem
    })
  }

  openMoreActions = editItem => event => {
    this.setState({
      editItem,
      anchorEl: event.currentTarget
    })
  }

  closeMoreActions = () => {
    this.setState({
      editItem: {},
      anchorEl: null
    })
  }

  openDialog = (dialogType) => (event) => {
    const {editItem} = this.state
    const {openDialog, setDialogType, setTodoItem} = this.props

    setTodoItem(editItem)
    setDialogType(dialogType)
    openDialog()
    this.closeMoreActions()
  }

  render () {
    const { classes, todoList } = this.props
    const {anchorEl} = this.state
    const actionList = [
      { name: CONSTANT.DETAIL, icon: <InfoIcon /> },
      { name: CONSTANT.UPDATE, icon: <EditIcon /> },
      { name: CONSTANT.DELETE, icon: <DeleteIcon /> }
    ]
    const tasksCount = todoList.reduce((tempCount, item) => item.done ? tempCount : tempCount += 1, 0)

    return (
      <Paper className={classes.root}>
        <List
          subheader={
            <ListSubheader
              component='div'>{tasksCount} Task{tasksCount > 1 ? 's' : ''} left
            </ListSubheader>
          }>
          {todoList.map((item, index) => (
            <ListItem
              key={item.key}
              role={item.key}
              dense
              className={classes.listItem}>
              <Checkbox
                color='primary'
                checked={item.done}
                onClick={this.updateTodoItem(item, 'done')}
                tabIndex={-1}
                disabled={item.isLoading} />
              <div className={item.done ? classes.taskDone : ''}>
                <Typography
                  color={item.done ? 'textSecondary' : 'inherit'}
                  className={classes.comment}>
                  {item.comment.split('\n')[0]}
                </Typography>
                <Typography color={item.done ? 'textSecondary' : 'inherit'}>
                  {`Dateline: ${format(item.dateline, 'YYYY/MM/DD HH:mm')}`}
                </Typography>
                <Typography color='textSecondary'>
                  {`Updated: ${formatDistance(item.createDT, new Date(), {
                    includeSeconds: true
                  })}`}
                </Typography>
              </div>
              <ListItemSecondaryAction>
                <IconButton
                  aria-label='Star'
                  onClick={this.updateTodoItem(item, 'star')}
                  disabled={item.isLoading}>
                  {item.star ? <StarIcon color='primary' /> : <StarBorderIcon />}
                </IconButton>
                <IconButton
                  aria-label='MoreVert'
                  onClick={this.openMoreActions(item)}
                  disabled={item.isLoading}>
                  {item.isLoading ? <CircularProgress className={classes.progress} /> : <MoreVertIcon />}
                </IconButton>
                <Menu
                  anchorEl={anchorEl}
                  open={Boolean(anchorEl)}
                  onClose={this.closeMoreActions}>
                  {actionList.map(action => (
                    <MenuItem
                      key={action.name}
                      onClick={this.openDialog(action.name)}
                      className={classes[action.name]}>
                      {action.icon}&nbsp;&nbsp;{action.name}
                    </MenuItem>
                  ))}
                </Menu>
              </ListItemSecondaryAction>
              {index === (todoList.length - 1) ? '' : <Divider />}
            </ListItem>
          ))}
        </List>
      </Paper>
    )
  }
}

AppList.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(AppList)


