import React, { Component } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import { withStyles } from '@material-ui/core/styles'
import * as CONSTANT from '../constant'

import Button from '@material-ui/core/Button'
import IconButton from '@material-ui/core/IconButton'
import Grid from '@material-ui/core/Grid'
import TextField from '@material-ui/core/TextField'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import withMobileDialog from '@material-ui/core/withMobileDialog'
import Slide from '@material-ui/core/Slide'
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';

import FileUploadIcon from '@material-ui/icons/FileUpload';
import CloseIcon from '@material-ui/icons/Close'
import CheckIcon from '@material-ui/icons/Check'
import AddIcon from '@material-ui/icons/Add'
import EditIcon from '@material-ui/icons/Edit'
import CommentIcon from '@material-ui/icons/Comment'
import DeleteIcon from '@material-ui/icons/Delete'

import red from '@material-ui/core/colors/red'
import green from '@material-ui/core/colors/green'

import AppDateTimePicker from './common/AppDateTimePicker'
import AppInputAdornment from './common/AppInputAdornment'

const styles = theme => ({
  title: {
    marginBottom: 16,
    fontSize: 14
  },
  pos: {
    marginBottom: 12
  },
  button: {
    margin: theme.spacing.unit
  },
  leftIcon: {
    marginRight: theme.spacing.unit
  },
  floatIcon: {
    position: 'fixed',
    right: '1rem',
    bottom: '1rem'
  },
  iconSmall: {
    fontSize: 20
  },
  cancelButton: {
    color: red[700]
  },
  saveButton: {
    color: green[700]
  },
  textField: {
    width: '100%'
  },
  flex: {
    flex: 1,
  },
  input: {
    display: 'none'
  },
  upload: {
    display: 'flex',
    justifyContent: 'center'
  },
  grid: {
    display: 'grid',
    textAlign: 'center'
  },
  image: {
    width: '100%',
    height: 'auto'
  }
})

const Transition = (props) => {
  return <Slide direction='up' {...props} />
}

class EditDialogContent extends Component {
  constructor () {
    super()
    this.state = {
      dialogTitle: '',
      actionButton: ''
    }
  }

  componentDidMount () {
    const {setTodoItem} = this.props
    setTodoItem({
      dateline: new Date()
    })
  }

  handleUploadChange = (event) => {
    const {setTodoItem} = this.props
    const [file] = event.target.files
    const fileReader = new FileReader()
    fileReader.addEventListener('load', () => {
      setTodoItem({imgUrl: fileReader.result})
    })
    fileReader.readAsDataURL(file)
  }

  handleInputChange = (event) => {
    const {setTodoItem} = this.props
    setTodoItem({
      [event.target.name]: event.target.value
    })
  }

  handleDateChange = (date) => {
    const {setTodoItem} = this.props
    setTodoItem({dateline: date})
  }

  onOpenDialog = () => {
    const {openDialog, setDialogType} = this.props
    setDialogType(CONSTANT.CREATE)
    openDialog()
  }

  onCreate = () => {
    const {addTodoItem, addLocationTodoItem, closeDialog, todoItem, currentRoute} = this.props
    const {dateline, comment, imgUrl} = todoItem
    const createDT = new Date()
    let payload = {
      comment,
      dateline: dateline ? dateline.toISOString() : createDT.toISOString(),
      createDT: createDT.toISOString()
    }

    if (imgUrl) {
      payload.imgUrl = imgUrl
    }

    addTodoItem(payload, currentRoute)
    addLocationTodoItem({
      ...payload,
      key: Date.now(),
      done: false,
      star: false
    })
    closeDialog()
  }

  onUpdate = () => {
    const {updateTodoItem, updateLocationTodoItem, closeDialog, todoItem, currentRoute} = this.props
    const payload = {
      key: todoItem.key,
      newTodoItem: {...todoItem}
    }

    updateTodoItem(payload, currentRoute)
    updateLocationTodoItem({...payload.newTodoItem})
    closeDialog()
  }

  onDelete = () => {
    const {deleteTodoItem, deleteLocationTodoItem, closeDialog, todoItem, currentRoute} = this.props
    const payload = {key: todoItem.key}

    deleteTodoItem(payload, currentRoute)
    deleteLocationTodoItem(todoItem)
    closeDialog()
  }

  updateUI = () => {
    const {dialogType, classes, closeDialog, todoItem} = this.props

    switch(dialogType) {
      case CONSTANT.CREATE:
        return {
          dialogTitle: 'Add a task',
          actionButton: (
            <Button
              size='small'
              className={classNames(classes.button, classes.saveButton)}
              onClick={this.onCreate}
              disabled={!todoItem.comment || todoItem.comment.length === 0}>
              <CheckIcon className={classNames(classes.leftIcon, classes.iconSmall)} />&nbsp;Create
            </Button>
          )
        }
      case CONSTANT.UPDATE:
        return {
          dialogTitle: 'Modify task',
          actionButton: (
            <Button
              size='small'
              className={classNames(classes.button, classes.saveButton)}
              onClick={this.onUpdate}
              disabled={!todoItem.comment || todoItem.comment.length === 0}>
              <EditIcon className={classNames(classes.leftIcon, classes.iconSmall)} />&nbsp;Update
            </Button>
          )
        }
      case CONSTANT.DELETE:
        return {
          dialogTitle: 'Delete task',
          actionButton: (
            <Button
              size='small'
              className={classNames(classes.button, classes.cancelButton)}
              onClick={this.onDelete}>
              <DeleteIcon className={classNames(classes.leftIcon, classes.iconSmall)} />&nbsp;Delete
            </Button>
          )
        }
      case CONSTANT.DETAIL:
      default:
        return {
          dialogTitle: 'Task information',
          actionButton: (
            <Button
              size='small'
              className={classes.button}
              onClick={closeDialog}>
              <CloseIcon className={classNames(classes.leftIcon, classes.iconSmall)} />&nbsp;Cancel
            </Button>
          )
        }
    }
  }

  updateUploadBtn = (readonly) => {
    const {todoItem} = this.props
    if (!readonly) {
      if (todoItem.imgUrl) {
        return (
          <label htmlFor='raised-button-file'>
            <Button variant='contained' color='primary' component='span'>
              <FileUploadIcon />&nbsp;&nbsp;Update Image
            </Button>
          </label>
        )
      } else {
        return (
          <label htmlFor='raised-button-file'>
            <Button variant='contained' color='primary' component='span'>
              <FileUploadIcon />&nbsp;&nbsp;Upload Image
            </Button>
          </label>
        )
      }
    }
    return '' 
  }
  
  render () {
    const {
      classes,
      closeDialog,
      dialogOpen,
      fullScreen,
      todoItem,
      dialogType
    } = this.props
    const {dialogTitle, actionButton} = this.updateUI()
    const readonly = (dialogType === CONSTANT.DETAIL || dialogType === CONSTANT.DELETE)

    return (
      <div>
        <Button 
          variant='fab' 
          color='primary' 
          aria-label='add' 
          className={classNames(classes.button, classes.floatIcon)}
          onClick={this.onOpenDialog}>
          <AddIcon />
        </Button>
        <Dialog
          fullScreen={fullScreen}
          TransitionComponent={Transition}
          open={dialogOpen}
          onClose={closeDialog}>
          <DialogContent>
            <Grid
              container
              justify='center'
              direction='row'
              alignItems='center'>
              <Grid item xs={12}>
                <Toolbar disableGutters>
                  <Typography variant='title' color='inherit' className={classes.flex}>
                    {dialogTitle}
                  </Typography>
                  <IconButton size='small' onClick={closeDialog}>
                    <CloseIcon />
                  </IconButton>
                </Toolbar>
              </Grid>
              <Grid item xs={12} className={classes.upload}>
                <div className={classes.grid}>
                  {!todoItem.imgUrl ? '' : <img
                    src={todoItem.imgUrl}
                    className={classes.image}
                    alt='upload' />}
                  <input
                    accept='image/*'
                    className={classes.input}
                    id='raised-button-file'
                    multiple
                    type='file'
                    onChange={this.handleUploadChange} />
                  {this.updateUploadBtn(readonly)}
                </div>
              </Grid>
              <Grid item xs={12}>
                <AppDateTimePicker
                  selectedDate={todoItem.dateline}
                  handleDateChange={this.handleDateChange}
                  disabled={readonly} />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  label='Comment'
                  multiline
                  rowsMax='4'
                  value={todoItem.comment}
                  name='comment'
                  onChange={this.handleInputChange}
                  className={classes.textField}
                  disabled={readonly}
                  margin='normal'
                  InputProps={{
                    startAdornment: <AppInputAdornment position='start' icon={<CommentIcon />} />
                  }} />
              </Grid>
            </Grid>
          </DialogContent>
          <DialogActions>
            {actionButton}
          </DialogActions>
        </Dialog>
      </div>
    )
  }
}

EditDialogContent.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(withMobileDialog()(EditDialogContent))



